import {
    OnInit,
    Directive,
    Input,
    ElementRef,
    AfterViewInit,
    Renderer2,
    OnDestroy,
    TemplateRef,
    ComponentFactory,
    ViewContainerRef,
    ComponentFactoryResolver,
    NgZone,
    ComponentRef,
} from "@angular/core";
import { OverLayHiddenService, CommonUtils } from "@farris/ui-common";
import { TooltipComponent } from "./tooltip-component/tooltip.component";
@Directive({
    selector: "[farrisTooltip]",
})
export class FarrisTooltipDirective
    implements OnInit, AfterViewInit, OnDestroy
{
    private tooltipEnable: boolean = true;
    private _componentRef: ComponentRef<TooltipComponent>;
    // 5æ7æ¥è¿½å è¿ç§åæ³ï¼æ§å¶ç¦ç¨ãå¯ç¨
    // åæ¥çæ¶åvalueæ¯ç©ºå­ç¬¦ä¸²
    @Input("farrisTooltip")
    set enableTooltip(value: any) {
        if (typeof value == "string" && value == "") {
            this.enableOrDisable(true);
        } else {
            this.enableOrDisable(value);
        }
    }
    get enableTooltip() {
        return this.tooltipEnable;
    }
    /* æç¤ºææ¬ */
    @Input() text = "";
    // æç¤ºæ¡æ ·å¼
    @Input() tipCls = "";
    // æç¤ºæ¡å®½åº¦
    @Input() tipWidth = "";
    /* ä½ç½® */
    @Input() placement = "top";

    @Input() zIndex = '';

    private triggerMode = "hover";

    /* è§¦åæ¹å¼ */
    @Input()
    set trigger(value) {
        if (value) {
            if (value !== this.triggerMode) {
                this.triggerMode = value;
                this.unbindEvent();
                this.bindEvent();
            }
        }
    }
    get trigger() {
        return this.triggerMode;
    }

    /* ç¦ç¨---è·å¶ä»æ§ä»¶çå±æ§å®¹æå²çªï¼éæ¸åºå¼ã
     * æ¯å¦æé®å¯ä»¥è®¾ç½®ç¦ç¨ä¸è§¦åç¹å»ï¼ä½æ³ç»åºç¹å»æç¤ºï¼è¿æ ·å°±å²çªäº
     */
    @Input()
    set disable(value) {
        this.enableOrDisable(!value);
    }
    get disable() {
        return !this.tooltipEnable;
    }

    @Input() showTip: boolean = true;
    // æ¨ªåçº æ­£çåç§
    @Input() rectifyReferenceH: Element | ElementRef | string;
    // çºµåçº æ­£çåç§
    @Input() rectifyReferenceV: Element | ElementRef | string;

    @Input() delay = 0;

    /* åå®¹æ¨¡æ¿ */
    @Input() content: string | TemplateRef<any>;

    mouseenterEvent: any;
    mouseleaveEvent: any;
    clickEvent: any;
    clickOutEvent: any;
    focusDownEvent: any;
    focusUpEvent: any;
    docBody: any;
    // æ¯å¦ç»å®äºäºä»¶
    private hasBindEvent = false;

    private delayTimer = null;

    /* çææç¤ºç»ä»¶ */
    componentFactory: ComponentFactory<TooltipComponent> =
        this.resolver.resolveComponentFactory(TooltipComponent);

    tooltipRef: any;
    tooltip: TooltipComponent;
    overLayService: OverLayHiddenService;
    commUtils: CommonUtils;
    constructor(
        private el: ElementRef,
        private render: Renderer2,
        private viewContainerRef: ViewContainerRef,
        private resolver: ComponentFactoryResolver
    ) {
        this.overLayService = new OverLayHiddenService();
        this.commUtils = new CommonUtils();
    }

    ngOnInit() {
        this.bindEvent();
    }

    ngAfterViewInit(): void {}

    ngOnDestroy() {
        this.unbindEvent();
        this.removeTooltip();

        this.overLayService.destory(this.el.nativeElement);
        this.overLayService = null;
    }
    private enableOrDisable(value) {
        this.tooltipEnable = value;
        if (this.tooltipEnable) {
            this.bindEvent();
        } else {
            this.unbindEvent();
        }
    }

    bindEvent() {
        if (!this.tooltipEnable) {
            return;
        }
        // æ¯å¦å·²ç»å®äºä»¶
        if (this.hasBindEvent) {
            return;
        }
        if (this.trigger === "click") {
            // é¼ æ ç¹å»
            this.clickEvent = this.render.listen(
                this.el.nativeElement,
                "click",
                (e: MouseEvent) => {
                    e.stopPropagation();
                    this.appendTooltip();
                    this.clickOutEvent = this.render.listen(
                        document,
                        "click",
                        () => {
                            this.removeTooltip();
                            this.clickOutEvent();
                        }
                    );
                }
            );
        } else if (this.trigger === "focus") {
            // èç¦
            // @Todo éè¦æinputç­ç»ä»¶åæ®éç»ä»¶åå¼
            this.focusDownEvent = this.render.listen(
                this.el.nativeElement,
                "mousedown",
                (e: MouseEvent) => {
                    e.preventDefault();
                    this.appendTooltip();
                }
            );
            this.focusUpEvent = this.render.listen(
                this.el.nativeElement,
                "mouseup",
                (e: MouseEvent) => {
                    e.stopPropagation();
                    this.removeTooltip();
                }
            );
        } else {
            // é¼ æ ç§»å¨ä¸å»
            this.mouseenterEvent = this.render.listen(
                this.el.nativeElement,
                "mouseenter",
                (e: MouseEvent) => {
                    e.stopPropagation();
                    if (this.delay) {
                        if (this.delayTimer) {
                            clearTimeout(this.delayTimer);
                        }
                        this.delayTimer = setTimeout(() => {
                            this.appendTooltip();
                            clearTimeout(this.delayTimer);
                        }, this.delay);
                    } else {
                        this.appendTooltip();
                    }
                }
            );
            this.mouseleaveEvent = this.render.listen(
                this.el.nativeElement,
                "mouseleave",
                (e: MouseEvent) => {
                    e.stopPropagation();
                    if (this.delayTimer) {
                        clearTimeout(this.delayTimer);
                    }
                    this.removeTooltip();
                }
            );
        }
        this.hasBindEvent = true;
    }

    /* è§£ç»äºä»¶ */
    unbindEvent() {
        // é¼ æ ç§»ä¸å»
        if (this.mouseenterEvent) {
            this.mouseenterEvent();
        }
        if (this.mouseleaveEvent) {
            this.mouseleaveEvent();
        }
        if (this.clickEvent) {
            this.clickEvent();
        }
        if (this.clickOutEvent) {
            this.clickOutEvent();
        }
        if (this.focusDownEvent) {
            this.focusDownEvent();
        }
        if (this.focusUpEvent) {
            this.focusUpEvent();
        }
        this.hasBindEvent = false;
    }

    /* body  ç§»é¤tooltip */
    removeTooltip() {
        if (this.tooltip) {
            this.render.removeChild(
                document.body,
                this.tooltip.el.nativeElement
            );
            this._componentRef.changeDetectorRef.markForCheck();
            this._componentRef.changeDetectorRef.detectChanges();
            this._componentRef.destroy();
            this.viewContainerRef.clear();
            this.tooltip = null;
            this._componentRef = null;

            this.overLayService.destory(this.el.nativeElement);
        }
    }

    private appendTooltip() {
        if (!this.showTip) return;
        this.generateTooltip();
        this.updateViewProps(
            this.placement,
            this.content,
            this.el.nativeElement.getBoundingClientRect(),
            this.getReferencePosition()
        );

        this.overLayService.registerMouseEvent(this.el.nativeElement, (e: MouseEvent) => {
            this.removeTooltip();
        });
    }

    /* æ´æ°tooltipç»ä»¶å±æ§ */
    private updateViewProps(
        placement: string,
        content: string | TemplateRef<any>,
        hostBoundingClientRect: object,
        referenceBoundingRect
    ) {
        this.tooltip.placement = placement;
        this.tooltip.content = content;
        this.tooltip.hostBoundingClientRect = hostBoundingClientRect;
        this.tooltip.tipCls = this.tipCls;
        this.tooltip.tipWidth = this.tipWidth;
        this.tooltip.referenceBoundingRect = referenceBoundingRect;
        this._componentRef.changeDetectorRef.markForCheck();
        this._componentRef.changeDetectorRef.detectChanges();
    }

    /* æé tooltipç»æ */
    private generateTooltip() {
        this._componentRef = this.viewContainerRef.createComponent(
            this.componentFactory
        );
        this.tooltip = this._componentRef.instance;
        this.tooltip.zIndex = '' + this.commUtils.getFloatingLayerIndex();
        

        // ç¶åç´ ä¸­ç§»é¤  æ·»å å°bodyä¸­
        this.render.removeChild(
            this.render.parentNode(this.el.nativeElement),
            this.tooltip.el.nativeElement
        );
        this.render.appendChild(document.body, this.tooltip.el.nativeElement);
    }

    /**
     * ç¡®è®¤åç§çè¾¹ç
     */
    private getReferencePosition() {
        let rRight = document.documentElement.clientWidth;
        let rBottom = document.documentElement.clientHeight;
        let rTop = 0;
        let rLeft = 0;
        // æ¨ªååç§
        if (this.rectifyReferenceH) {
            let rectifyReferenceHEl = this.getRectifyReferenceElement(
                this.rectifyReferenceH
            );
            rRight = rectifyReferenceHEl.getBoundingClientRect().right;
            rLeft = rectifyReferenceHEl.getBoundingClientRect().left;
        }
        // çºµååç§
        if (this.rectifyReferenceV) {
            let rectifyReferenceVEl = this.getRectifyReferenceElement(
                this.rectifyReferenceV
            );
            rBottom = rectifyReferenceVEl.getBoundingClientRect().bottom;
            rTop = rectifyReferenceVEl.getBoundingClientRect().top;
        }
        return { top: rTop, left: rLeft, right: rRight, bottom: rBottom };
    }
    /**
     * è·åçº æ­£åç´ 
     */
    private getRectifyReferenceElement(referenceEl) {
        if (referenceEl instanceof ElementRef) {
            return referenceEl.nativeElement;
        } else if (typeof referenceEl == "string") {
            return document.querySelector(referenceEl as string);
        }
        return referenceEl;
    }
}
